#header{position:fixed;z-index:1;width:100%;height:2.5rem;background-color:rgba(255,255,255,0.8);border-bottom:1px solid #ccc;box-shadow:0 .1rem .1rem #ccc}#header .header-index{text-align:center;line-height:2.5rem;font-size:1.3rem;font-weight:bold;font-family:黑体;letter-spacing:1.5rem}#header .more{position:absolute;left:0;top:0;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;height:100%;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;margin-left:1rem}#header .more span{display:inline-block;width:1.7rem;height:.1rem;background-color:#444;margin:.2rem}.mask{position:fixed;z-index:50;width:100%;height:100%;left:0;top:0;background-color:rgba(0,0,0,0.3)}.scroll-stop{overflow:hidden}


