#menu{position:fixed;top:0;left:-14rem;width:14rem;height:100%;font-size:1.1rem;z-index:100;background-color:#fff;-webkit-transform:translateZ(0);transform:translateZ(0);-webkit-transition:all .3s ease;transition:all .3s ease;box-sizing:border-box}#menu .menu-list{background-color:#fff;box-sizing:border-box;padding:2rem 1.5rem 0 1.5rem;width:100%}#menu .menu-list-item{padding:.5rem 0;text-align:center}#menu.slide{-webkit-transform:translateX(14rem);transform:translateX(14rem)}#menu .avatar{width:3.5rem;height:3.5rem;border-radius:50%}#menu .userName{font-size:1.1rem}#menu .login{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-webkit-box-align:center;-ms-flex-align:center;align-items:center}#menu .arrow{font-size:.9rem;color:#f00}


