@font-face {
  font-family: 'iconfont';
  src: url('//at.alicdn.com/t/font_1472043088_9693105.eot'); /* IE9*/
  src: url('//at.alicdn.com/t/font_1472043088_9693105.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('//at.alicdn.com/t/font_1472043088_9693105.woff') format('woff'), /* chrome、firefox */
  url('//at.alicdn.com/t/font_1472043088_9693105.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
  url('//at.alicdn.com/t/font_1472043088_9693105.svg#iconfont') format('svg'); /* iOS 4.1- */
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 1.1rem;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
.iconfont::before{margin-right: 2.5rem; font-size: 1.2rem}
.icon-all::before{content: '\e606';}
.icon-share::before{content: '\e600';}
.icon-ask::before{content: '\e604';}
.icon-good::before{content: '\e602';}
.icon-job::before{content: '\e607';}
.icon-msg::before{content: '\e601';}
.icon-about::before{content: '\e603';}
.icon-login::before{content: '\e605'; color: #f00;}
                    


